.resize-drag {
    background-color: #29e;
    color: white;
    font-size: 20px;
    font-family: sans-serif;
    border-radius: 8px;
    padding: 20px;
    margin: 30px 20px;
    touch-action: none;
  
    width: 120px;
  
    /* This makes things *much* easier */
    box-sizing: border-box;
  }
  
  .resize-container {
    display: inline-block;
    width: 100%;
    height: 240px;
  }